import axios from "axios";

// url     
const api = axios.create({
  baseURL: "http://43.201.179.98:80/api/",
  headers: { "Content-type": "application/json" }, // data type
});

// Add a request interceptor
api.interceptors.request.use(
  function (config) {
    const accessToken = localStorage.getItem("accessToken");

    // AccessToken  
    if (accessToken) {
      config.headers["Authorization"] = "Bearer " + accessToken;
    }
    return config;
  },
  function (error) {
    // Do something with request error
    return Promise.reject(error);
  }
);

// Add a response interceptor
api.interceptors.response.use(
  function (response) {
    // Any status code that lie within the range of 2xx cause this function to trigger
    // Do something with response data
    return response;
  },
  async (error) => {
    const {
      config,
      response: { status },
    } = error;
    if (true) {
      const originalRequest = config;
      const accessToken = localStorage.getItem("accessToken");
      const refreshToken = localStorage.getItem("refreshToken");

      const token = {
        accessToken: accessToken,
        refreshToken: refreshToken,
      };
      // token refresh 
      const { data } = await axios.post(
        `http://43.201.179.98:80/api/auth/reissue`, // token refresh api
        token,
        { headers: { authorization: `Bearer ${accessToken}` } }
      );
      //   
      // dispatch(userSlice.actions.setAccessToken(data.data.accessToken)); store 
      const newAccessToken = data.accessToken;
      const newRefreshToken = data.refreshToken;
      await localStorage.setItem("accessToken", newAccessToken);
      await localStorage.setItem("refreshToken", newRefreshToken);
      originalRequest.headers.authorization = `Bearer ${newAccessToken}`;
      // 401     accessToken 
      return axios(originalRequest);
    }
    // Any status codes that falls outside the range of 2xx cause this function to trigger
    // Do something with response error
    return Promise.reject(error);
  }
);

export default api;
